import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import localFont from "next/font/local";
import "./globals.css";
// import GridDistortion from "../components/GridDistortion";
import Prism from "../components/PrismaBackground";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

const chineseFont = localFont({
  src: "../../public/Fonts/MFYueHei_Noncommercial-Regular.otf",
  variable: "--font-chinese",
  display: "swap",
});

const scifiFont = localFont({
  src: "../../public/Fonts/Ethnocentric Rg.otf",
  variable: "--font-scifi",
  display: "swap",
});

export const metadata: Metadata = {
  title: "Prism",
  description: "Prism Website",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body
        className={`${geistSans.variable} ${geistMono.variable} ${chineseFont.variable} ${scifiFont.variable} antialiased`}
        style={{margin: 0, padding: 0, overflow: 'auto'}}
      >
        {/* Fixed background GridDistortion for all pages - stays in place during scroll */}
        <div className="fixed inset-0 w-full h-full z-0 overflow-hidden bg-black" style={{position: 'fixed', top: 0, left: 0, right: 0, bottom: 0}}>
          <Prism
              animationType="3drotate"
              timeScale={1}
              height={2.5}
              baseWidth={3.5}
              hueShift={0.02}
              colorFrequency={3}
              noise={0.1}
              glow={0.1}
              scale={3.6}
              colorScheme="champagne-chrome"
          />
        </div>
        
        {/* Main content with higher z-index */}
        <div className="relative z-10">
          {children}
        </div>
      </body>
    </html>
  );
}
